<script>
  export default {
    name: "page4",
    methods: {
      // 计算平均数
      avg(arr) {
        return arr.reduce((a, b) => a + b, 0) / arr.length;
      },
      // 计算最小公倍数
      lcm(a, b) {
        return a * b / this.gcd(a, b);
      },
      // 计算最大公约数
      gcd(a, b) {
        return b ? this.gcd(b, a % b) : a;
      }
    }
  }
</script>

